<template>
  <div>

    <div>
      <hr>
      <b-form-group label="Hyaline Tip's shape">
        <b-form-radio
          v-model="selected"
          name="some-radios"
          value="1"
          @change="change">
          Elongated tip, pointed
          <br>
          <b-img
            :height="150"
            :src="require('@/assets/Macrotermes/mac_001.png')"
            alt="Fluid image"/>
          <br>
          <!-- (Ahmad, 1965) -->
        </b-form-radio>
        <br>
        <br>
        <slot name="1"/>
        <br>
        <hr>
        <b-form-radio
          v-model="selected"
          name="some-radios"
          value="2"
          @change="change">

          Short tip, tongue like - anteriorly rounded

          <br>
          <b-img
            :height="150"
            :src="require('@/assets/Macrotermes/mac_002.png')"
            alt="Fluid image"/>
          <br>
          <!-- (Ahmad, 1965; Maiti, 1983) -->
        </b-form-radio>

        <br>
        <br>
        <slot name="2"/>
        <br>

        <b-form-radio
          v-model="selected"
          name="some-radios"
          value="3"
          @change="change">

          Short tip, trilobed pointy

          <br>
          <b-img
            :height="150"
            :src="require('@/assets/Macrotermes/mac_003.png')"
            alt="Fluid image"/>
          <br>
          <!-- (Ahmad, 1965; Maiti, 1983) -->
        </b-form-radio>

        <br>
        <br>
        <slot name="3"/>
        <br>
      </b-form-group>
      <hr>
    </div>

  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: [String, Array, Object, Number, Boolean],
      default: null,
    },
  },
  data() {
    return {
      selected: '',
    };
  },
  watch: {
    selected(newVal) {
      if (newVal) {
        this.isFinished(newVal);
      } else {
        this.$emit('phaseTwo', null);
      }
    },
    data(newVal) {
      if (newVal === '1') {
        this.$emit('phaseTwo', null);
      }
    },
  },
  methods: {
    change(newVal) {
      this.selected = newVal;
    },

    isFinished() {
      this.$emit('phaseTwo', this.selected);
    },
  },

};
</script>



